body::-webkit-scrollbar {
  width: 1em;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

.loader-bolita div {
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.loader-bolita div:nth-child(1) {
  left: 8px;
  animation: loader-bolita1 0.6s infinite;
}

.loader-bolita div:nth-child(2) {
  left: 8px;
  animation: loader-bolita2 0.6s infinite;
}

.loader-bolita div:nth-child(3) {
  left: 32px;
  animation: loader-bolita2 0.6s infinite;
}

.loader-bolita div:nth-child(4) {
  left: 56px;
  animation: loader-bolita3 0.6s infinite;
}

@keyframes loader-bolita1 {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes loader-bolita3 {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes loader-bolita2 {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(24px, 0);
  }
}

